/* Modern gradient background animation */
@keyframes gradientBG {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Glow animation */
@keyframes glow {
  0% { box-shadow: 0 0 5px rgba(108, 92, 231, 0.5); }
  50% { box-shadow: 0 0 20px rgba(108, 92, 231, 0.8); }
  100% { box-shadow: 0 0 5px rgba(108, 92, 231, 0.5); }
}

/* Button hover animation */
@keyframes buttonHover {
  0% { transform: translateY(0); }
  50% { transform: translateY(-3px); }
  100% { transform: translateY(0); }
}

.App {
  text-align: center;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  min-height: 100vh;
  transition: all 0.3s ease;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradientBG 15s ease infinite;
  color: white;
}

.App-header {
  background: rgba(0, 0, 0, 0.2);
  padding: 20px;
  border-radius: 15px;
  margin-bottom: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
}

.App-header h1 {
  margin: 0;
  font-size: 2.5em;
  background: linear-gradient(to right, #fff, #ececec);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.mood-section {
  margin: 20px 0;
  display: flex;
  gap: 10px;
  justify-content: center;
}

.mood-input {
  width: 300px;
  padding: 12px;
  border: none;
  border-radius: 10px;
  font-size: 14px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
  color: white;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.mood-input:focus {
  outline: none;
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.02);
}

.mood-input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.mood-button {
  padding: 12px 24px;
  background: linear-gradient(45deg, #6c5ce7, #a367dc);
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s ease;
  animation: glow 2s infinite;
}

.mood-button:hover:not(:disabled) {
  animation: buttonHover 1s infinite;
  background: linear-gradient(45deg, #a367dc, #6c5ce7);
}

.input-section {
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: center;
}

.prompt-input {
  width: 80%;
  height: 100px;
  padding: 15px;
  border: none;
  border-radius: 15px;
  font-size: 14px;
  resize: vertical;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
  color: white;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.select-input {
  width: 200px;
  padding: 10px;
  border: none;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

.select-input option {
  background: #2c3e50;
  color: white;
}

.button-group {
  display: flex;
  gap: 15px;
  justify-content: center;
  flex-wrap: wrap;
}

.generate-button {
  padding: 12px 24px;
  background: linear-gradient(45deg, #4CAF50, #45a049);
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.format-button {
  padding: 12px 24px;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.meme-button {
  background: linear-gradient(45deg, #e17055, #ff7675);
}

.video-button {
  background: linear-gradient(45deg, #00b894, #00cec9);
}

.generate-button:hover:not(:disabled),
.format-button:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

.output-section {
  margin: 20px 0;
  padding: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  text-align: left;
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
}

.ai-response {
  white-space: pre-wrap;
  padding: 20px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: white;
  font-size: 1.1em;
  line-height: 1.6;
}

.news-section {
  margin: 20px 0;
  text-align: left;
}

.news-item {
  padding: 20px;
  margin: 15px 0;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  transition: all 0.3s ease;
}

.news-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px 0 rgba(31, 38, 135, 0.45);
}

.news-item h3 {
  margin-top: 0;
  color: #fff;
  font-size: 1.3em;
}

.news-item a {
  color: #74b9ff;
  text-decoration: none;
  font-weight: bold;
  transition: all 0.3s ease;
}

.news-item a:hover {
  color: #0984e3;
  text-decoration: underline;
}

/* Disabled states */
.mood-button:disabled,
.generate-button:disabled,
.format-button:disabled {
  background: linear-gradient(45deg, #808080, #a8a8a8);
  cursor: not-allowed;
  animation: none;
  transform: none;
}

/* Responsive design */
@media (max-width: 768px) {
  .App {
    padding: 10px;
  }
  
  .prompt-input {
    width: 90%;
  }
  
  .button-group {
    flex-direction: column;
    align-items: center;
  }
  
  .format-button,
  .generate-button {
    width: 100%;
    max-width: 300px;
  }
}